// Minimax (+Alpha-Beta) Implementation

import { BoardSize, Difficulty, PieceType } from './enum';
import { Piece } from './types';

let _ = require('lodash');

const DEPTH_FOR_EASY = 2;
const DEPTH_FOR_NORMAL = 4;
const DEPTH_FOR_HARD = 12;

type Position = { x: number; y: number };

type MovesState = {
  movesPattern: string;
  depth: number;
  score: number;
};

type ComputerState = {
  move: Position[] | null;
  score: number;
};

type PositionPair = [number, number];

const maxScore = 100000;
let previousStatesMoves: MovesState[] = [];
let firstMove = true;

export const playAI = (
  brd: Piece[],
  boardSize: BoardSize,
  difficulty: Difficulty
): ComputerState => {
  const size = parseInt(boardSize);
  const board = JSON.parse(JSON.stringify(brd));
  let k = 0;
  previousStatesMoves = [];
  firstMove = true;
  let dummyBoard = Array.apply(null, Array(size)).map(() => new Array(size));
  for (let i = 0; i < size; i++) {
    for (let j = 0; j < size; j++) {
      dummyBoard[i][j] = board[k].type;
      k++;
    }
  }

  if (difficulty === Difficulty.normal) {
    return generateDecision(dummyBoard, DEPTH_FOR_NORMAL);
  } else if (difficulty === Difficulty.hard) {
    return generateDecision(dummyBoard, DEPTH_FOR_HARD);
  }

  return generateDecision(dummyBoard, DEPTH_FOR_EASY);
};

const generateDecision = (
  board: PieceType[][],
  depth: number
): ComputerState => {
  // let startAI = new Date().getTime();

  const piecesLeft = 15;

  // pick random move if too many pieces on the board
  // if (
  //   _.flattenDeep(board).filter((p: PieceType) => p === PieceType.piece)
  //     .length > piecesLeft
  // ) {
  //   const initialMoves = calculateMoves(board);
  //   return { move: initialMoves[0], score: 0 };
  // }

  // Algorithm call
  const aiMove = maximizePlay(board, depth);

  // let endAI = new Date().getTime() - startAI;

  return aiMove;
};

const maximizePlay = (
  board: PieceType[][],
  depth: number,
  alpha: number = 0,
  beta: number = 0
): ComputerState => {
  const score = -calculateScore(board);

  if (isFinished(depth, score)) return { move: null, score };

  const possibleMoves = calculateMoves(board);

  let max: ComputerState = { move: null, score: -99999 };

  const alreadyCalculated = previousStatesMoves.find(
    p =>
      _.isEqual(p.movesPattern, _.countBy(possibleMoves, 'length')) &&
      p.depth === depth
  );

  if (alreadyCalculated) {
    return { move: null, score: alreadyCalculated.score };
  }

  // For all possible moves
  for (let m = 0; m < possibleMoves.length; m++) {
    let newState = copyBoard(board, possibleMoves[m]); // Create new board
    const nextMove = minimizePlay(newState, depth - 1, alpha, beta); // Recursive calling
    // Evaluate new move
    if (max.move === null || nextMove.score > max.score) {
      max.move = possibleMoves[m];
      max.score = nextMove.score;
      alpha = nextMove.score;
    }

    if (alpha >= beta) {
      return max;
    }
  }
  previousStatesMoves.push({
    movesPattern: _.countBy(possibleMoves, 'length'),
    depth,
    score: max.score,
  });
  return max;
};

const minimizePlay = (
  board: PieceType[][],
  depth: number,
  alpha: number = 0,
  beta: number = 0
): ComputerState => {
  const score = calculateScore(board);

  if (isFinished(depth, score)) return { move: null, score };

  let min: ComputerState = { move: null, score: 99999 };

  const possibleMoves = calculateMoves(board);

  const alreadyCalculated = previousStatesMoves.find(
    p =>
      _.isEqual(p.movesPattern, _.countBy(possibleMoves, 'length')) &&
      p.depth === depth
  );
  if (alreadyCalculated) {
    return { move: null, score: alreadyCalculated.score };
  }

  // For all possible moves
  for (let m = 0; m < possibleMoves.length; m++) {
    let newState = copyBoard(board, possibleMoves[m]);
    let nextMove = maximizePlay(newState, depth - 1, alpha, beta);
    if (min.move == null || nextMove.score < min.score) {
      min.move = possibleMoves[m];
      min.score = nextMove.score;
      beta = nextMove.score;
    }

    if (alpha >= beta) {
      return min;
    }
  }
  previousStatesMoves.push({
    movesPattern: _.countBy(possibleMoves, 'length'),
    depth,
    score: min.score,
  });
  return min;
};

const isFinished = (depth: number, score: number): boolean =>
  depth === 0 || score === maxScore || score === -maxScore;

const calculateMoves = (board: PieceType[][]) => {
  let horizontalMoves: [PositionPair[]] = [] as any;
  let hMove: PositionPair[] = [];

  for (let i = 0; i < board.length; i++) {
    if (hMove.length !== 0) horizontalMoves.push(hMove);
    hMove = [];
    for (let j = 0; j < board.length; j++) {
      if (board[i][j] !== PieceType.obstacle) {
        if (board[i][j] !== PieceType.empty) {
          hMove.push([i, j]);
        }
      } else if (hMove.length !== 0) {
        horizontalMoves.push(hMove);
        hMove = [];
      }
    }
  }
  if (hMove.length !== 0) horizontalMoves.push(hMove);

  let verticalMoves: [PositionPair[]] = [] as any;
  let vMove: PositionPair[] = [];

  for (let j = 0; j < board.length; j++) {
    if (vMove.length !== 0) verticalMoves.push(vMove);
    vMove = [];
    for (let i = 0; i < board.length; i++) {
      if (board[i][j] !== PieceType.obstacle) {
        if (board[i][j] !== PieceType.empty) {
          vMove.push([i, j]);
        }
      } else if (vMove.length !== 0) {
        verticalMoves.push(vMove);
        vMove = [];
      }
    }
  }
  if (vMove.length !== 0) verticalMoves.push(vMove);

  const allMoves = getAllMoves(_.concat(horizontalMoves, verticalMoves), board);
  if (firstMove) {
    firstMove = false;
    return _.shuffle(allMoves); // shuffle the moves so that in case of score 0, the comp will pick a random move
  }

  return allMoves;
};

const calculateScore = (board: PieceType[][]): number => {
  const lastPiece =
    _.flattenDeep(board).filter((p: PieceType) => p === PieceType.piece)
      .length === 1;
  if (lastPiece) {
    return maxScore;
  } else {
    return 0;
  }
};

// create board after move
const copyBoard = (board: PieceType[][], move) => {
  let newBoard = JSON.parse(JSON.stringify(board));
  for (let i = 0; i < move.length; i++) {
    newBoard[move[i][0]][move[i][1]] = PieceType.empty;
  }

  return newBoard;
};

const getAllMoves = (array, board: PieceType[][]) => {
  let combos = [];
  for (let i = 0; i < array.length; i++) {
    for (let k = array[i].length; k > 0; k--) {
      for (let j = 0; j < k - 1; j++) {
        combos.push(array[i].slice(j, k));
      }
    }
  }
  for (let j = 0; j < board.length; j++) {
    for (let i = 0; i < board.length; i++) {
      if (board[i][j] === PieceType.piece) {
        combos.push([[i, j]]);
      }
    }
  }

  return combos;
};
